import React from "react";
import { ThemeContext } from "./ThemeContext";

const formatBytes = (bytes) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};

const downloadAttachment = (attachmentName) => {
    // Placeholder function for downloading a single attachment
    console.log(`Downloading attachment: ${attachmentName}`);
};

export default function Attachment({ attachment, onDownload }) {
    const { colors } = React.useContext(ThemeContext);
    return (
        <li 
            key={attachment.id}
            style={{ 
            display: 'flex', 
            justifyContent: 'space-between', 
            alignItems: 'center', 
            padding: '8px 12px', 
            marginBottom: '8px',
            borderRadius: '4px',
            backgroundColor: colors.listBg,
            border: `1px solid ${colors.listBorder}`,
            fontSize: '13px',
            }}
        >
            <div style={{ flexGrow: 1, marginRight: '10px' }}>
            <div style={{ fontWeight: '500', color: colors.textColor }}>{attachment.name}</div>
            <div style={{ color: colors.labelColor, fontSize: '11px' }}>{formatBytes(attachment.size)}</div>
            </div>
            <button
            onClick={() => downloadAttachment(attachment.name)}
            style={{
                padding: "5px 10px",
                borderRadius: "4px",
                border: "1px solid " + colors.primaryColor,
                backgroundColor: "transparent",
                color: colors.primaryColor,
                cursor: "pointer",
                fontSize: "12px",
                transition: "background-color 0.2s ease",
            }}
            onMouseOver={(e) => { e.target.style.backgroundColor = colors.primaryColor; e.target.style.color = '#fff'; }}
            onMouseOut={(e) => { e.target.style.backgroundColor = 'transparent'; e.target.style.color = colors.primaryColor; }}
            >
            Download
            </button>
        </li>
    );
}
