import React from "react";
import { ThemeContext } from "./ThemeContext";
import Attachment from "./Attachment";

const downloadAllAttachments = () => {
    // Placeholder function for downloading all attachments
    console.log("Downloading all attachments");
};

export default function AttachmentsList({ attachments }) {
    const { colors } = React.useContext(ThemeContext);
    if (!attachments || attachments.length === 0) {
        return <div></div>;
    }

    return (
      <div style={{ borderTop: `1px solid ${colors.listBorder}`, paddingTop: "20px" }}>
        <h3 style={{ margin: "0 0 15px 0", fontSize: "16px", color: colors.textColor }}>
          Attachments ({attachments.length})
        </h3>
        
        <ul style={{ listStyle: 'none', padding: 0, margin: 0 }}>
          {attachments.map((attachment) => <Attachment key={attachment.id} attachment={attachment} />)}
        </ul>

        <div style={{ textAlign: "center", marginTop: "20px" }}>
          <button
            onClick={downloadAllAttachments}
            disabled={attachments.length === 0}
            style={{
              padding: "10px 20px",
              borderRadius: "6px",
              border: "none",
              backgroundColor: colors.primaryColor,
              color: "#fff",
              fontWeight: "bold",
              cursor: attachments.length === 0 ? "not-allowed" : "pointer",
              transition: "background-color 0.2s ease",
            }}
            onMouseOver={(e) => e.target.style.backgroundColor = colors.primaryColorHover}
            onMouseOut={(e) => e.target.style.backgroundColor = colors.primaryColor}
          >
            Download All ({attachments.length})
          </button>
        </div>
      </div>
    );
}